
.floating-chat{position:fixed;right:0px;bottom:0px;z-index:1000
}
.chat-heading{cursor:pointer
}
.chat-heading .icon-comment-empty{color:#b9b9ba;color:var(--fg, #b9b9ba)
}
.chat-window{width:345px;max-height:40vh;overflow-y:auto;overflow-x:hidden
}
.chat-message{display:-ms-flexbox;display:flex;padding:0.2em 0.5em
}
.chat-avatar img{height:24px;width:24px;border-radius:4px;border-radius:var(--avatarRadius, 4px);margin-right:0.5em;margin-top:0.25em
}
.chat-input{display:-ms-flexbox;display:flex
}
.chat-input textarea{-ms-flex:1;flex:1;margin:0.6em;min-height:3.5em;resize:none
}



