}
.post-status-form .visibility-tray{font-size:1.2em;padding:3px;cursor:pointer
}
.post-status-form .visibility-tray .selected{color:#b9b9ba;color:var(--lightFg, #b9b9ba)
}
.visibility-notice{padding:.5em;border:1px solid rgba(185,185,186,0.5);border:1px solid var(--faint, rgba(185,185,186,0.5));border-radius:4px;border-radius:var(--inputRadius, 4px)
}
.post-status-form .form-bottom,.login .form-bottom{display:-ms-flexbox;display:flex;padding:0.5em;height:32px
}
.post-status-form .form-bottom button,.login .form-bottom button{width:10em
}
.post-status-form .form-bottom p,.login .form-bottom p{margin:0.35em;padding:0.35em;display:-ms-flexbox;display:flex
}
.post-status-form .error,.login .error{text-align:center
}
.post-status-form .attachments,.login .attachments{padding:0 0.5em
}
.post-status-form .attachments .attachment,.login .attachments .attachment{position:relative;border:1px solid #222;border:1px solid var(--border, #222);margin:0.5em 0.8em 0.2em 0
}
.post-status-form .attachments i,.login .attachments i{position:absolute;margin:10px;padding:5px;background:rgba(230,230,230,0.6);border-radius:10px;border-radius:var(--attachmentRadius, 10px);font-weight:bold
}
.post-status-form .btn,.login .btn{cursor:pointer
}
.post-status-form .btn[disabled],.login .btn[disabled]{cursor:not-allowed
}
.post-status-form form,.login form{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;padding:0.6em
}
.post-status-form .form-group,.login .form-group{display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;padding:0.3em 0.5em 0.6em;line-height:24px
}
.post-status-form form textarea.form-cw,.login form textarea.form-cw{line-height:16px;resize:none;overflow:hidden;transition:min-height 200ms 100ms;min-height:1px
}
.post-status-form form textarea.form-control,.login form textarea.form-control{line-height:16px;resize:none;overflow:hidden;transition:min-height 200ms 100ms;min-height:1px;box-sizing:content-box
}
.post-status-form form textarea.form-control:focus,.login form textarea.form-control:focus{min-height:48px
}
.post-status-form .btn,.login .btn{cursor:pointer
}
.post-status-form .btn[disabled],.login .btn[disabled]{cursor:not-allowed
}
.post-status-form .icon-cancel,.login .icon-cancel{cursor:pointer;z-index:4
}
.post-status-form .autocomplete-panel,.login .autocomplete-panel{margin:0 0.5em 0 0.5em;border-radius:5px;border-radius:var(--tooltipRadius, 5px);position:absolute;z-index:1;box-shadow:1px 2px 4px rgba(0,0,0,0.5);min-width:75%;background:#121a24;background:var(--bg, #121a24);color:#b9b9ba;color:var(--lightFg, #b9b9ba)
}
.post-status-form .autocomplete,.login .autocomplete{cursor:pointer;padding:0.2em 0.4em 0.2em 0.4em;border-bottom:1px solid rgba(0,0,0,0.4);display:-ms-flexbox;display:flex
}
.post-status-form .autocomplete img,.login .autocomplete img{width:24px;height:24px;border-radius:4px;border-radius:var(--avatarRadius, 4px);object-fit:contain
}
.post-status-form .autocomplete span,.login .autocomplete span{line-height:24px;margin:0 0.1em 0 0.2em
}
.post-status-form .autocomplete small,.login .autocomplete small{margin-left:.5em;color:rgba(185,185,186,0.5);color:var(--faint, rgba(185,185,186,0.5))
}
.post-status-form .autocomplete.highlighted,.login .autocomplete.highlighted{background-color:#182230;background-color:var(--btn, #182230)
}



