
.still-image{position:relative;line-height:0;overflow:hidden;width:100%;height:100%
}
.still-image:hover canvas{display:none
}
.still-image img{width:100%;height:100%
}
.still-image.animated:hover::before,.still-image.animated img{visibility:hidden
}
.still-image.animated:hover img{visibility:visible
}
.still-image.animated::before{content:'gif';position:absolute;line-height:10px;font-size:10px;top:5px;left:5px;background:rgba(127,127,127,0.5);color:#FFF;display:block;padding:2px 4px;border-radius:5px;border-radius:var(--tooltipRadius, 5px);z-index:2
}
.still-image canvas{position:absolute;top:0;bottom:0;left:0;right:0;width:100%;height:100%
}



