
.profile-panel-background{background-size:cover;border-radius:10px;border-radius:var(--panelRadius, 10px)
}
.profile-panel-background .panel-heading{padding:0.6em 0em;text-align:center
}
.profile-panel-body{word-wrap:break-word;background:linear-gradient(to bottom, transparent, #121a24 80%);background:linear-gradient(to bottom, transparent, var(--bg, #121a24) 80%)
}
.user-info{color:#b9b9ba;color:var(--lightFg, #b9b9ba);padding:0 16px
}
.user-info .container{padding:16px 10px 6px 10px;display:-ms-flexbox;display:flex;max-height:56px;overflow:hidden
}
.user-info .container .avatar{border-radius:4px;border-radius:var(--avatarRadius, 4px);-ms-flex:1 0 100%;flex:1 0 100%;width:56px;height:56px;box-shadow:0px 1px 8px rgba(0,0,0,0.75);object-fit:cover
}
.user-info .container .avatar.animated::before{display:none
}
.user-info:hover .animated.avatar canvas{display:none
}
.user-info:hover .animated.avatar img{visibility:visible
}
.user-info .usersettings{color:#b9b9ba;color:var(--lightFg, #b9b9ba);opacity:.8
}
.user-info .name-and-screen-name{display:block;margin-left:0.6em;text-align:left;text-overflow:ellipsis;white-space:nowrap;-ms-flex:1 1 0px;flex:1 1 0
}
.user-info .user-name{text-overflow:ellipsis;overflow:hidden
}
.user-info .user-screen-name{color:#b9b9ba;color:var(--lightFg, #b9b9ba);display:inline-block;font-weight:light;font-size:15px;padding-right:0.1em
}
.user-info .user-meta{margin-bottom:.4em
}
.user-info .user-meta .following{font-size:14px;-ms-flex:0 0 100%;flex:0 0 100%;margin:0;padding-left:16px;text-align:left;float:left
}
.user-info .user-meta .floater{margin:0
}
.user-info .user-meta::after{display:block;content:'';clear:both
}
.user-info .user-interactions{display:-ms-flexbox;display:flex;-ms-flex-flow:row wrap;flex-flow:row wrap;-ms-flex-pack:justify;justify-content:space-between
}
.user-info .user-interactions div{-ms-flex:1;flex:1
}
.user-info .user-interactions .mute{max-width:220px;min-height:28px
}
.user-info .user-interactions .remote-follow{max-width:220px;min-height:28px
}
.user-info .user-interactions .follow{max-width:220px;min-height:28px
}
.user-info .user-interactions button{width:92%;height:100%
}
.user-info .user-interactions .remote-button{height:28px !important;width:92%
}
.user-info .user-interactions .pressed{border-bottom-color:rgba(255,255,255,0.2);border-top-color:rgba(0,0,0,0.2)
}
.user-counts{display:-ms-flexbox;display:flex;line-height:16px;padding:.5em 1.5em 0em 1.5em;text-align:center;-ms-flex-pack:justify;justify-content:space-between;color:#b9b9ba;color:var(--lightFg, #b9b9ba)
}
.user-counts.clickable .user-count{cursor:pointer
}
.user-counts.clickable .user-count:hover:not(.selected){transition:border-bottom 100ms;border-bottom:3px solid #d8a070;border-bottom:3px solid var(--link, #d8a070)
}
.user-count{-ms-flex:1;flex:1;padding:.5em 0 .5em 0;margin:0 .5em
}
.user-count.selected{transition:none;border-bottom:5px solid #d8a070;border-bottom:5px solid var(--link, #d8a070);border-radius:4px;border-radius:var(--btnRadius, 4px)
}
.user-count h5{font-size:1em;font-weight:bolder;margin:0 0 0.25em
}
.user-count a{text-decoration:none
}
.dailyAvg{margin-left:1em;font-size:0.7em;color:#CCC
}
.floater{float:right;margin-top:16px
}
.floater .userHighlightCl{padding:2px 10px
}
.floater .userHighlightSel,.floater .userHighlightSel.select{padding-top:0;padding-bottom:0
}
.floater .userHighlightSel.select i{line-height:22px
}
.floater .userHighlightText{width:70px
}
.floater .userHighlightCl,.floater .userHighlightText,.floater .userHighlightSel,.floater .userHighlightSel.select{height:22px;vertical-align:top;margin-right:0
}



