
.nav-panel .panel{overflow:hidden
}
.nav-panel ul{list-style:none;margin:0;padding:0
}
.nav-panel li{border-bottom:1px solid;border-color:#222;border-color:var(--border, #222);padding:0
}
.nav-panel li:first-child a{border-top-right-radius:10px;border-top-right-radius:var(--panelRadius, 10px);border-top-left-radius:10px;border-top-left-radius:var(--panelRadius, 10px)
}
.nav-panel li:last-child a{border-bottom-right-radius:10px;border-bottom-right-radius:var(--panelRadius, 10px);border-bottom-left-radius:10px;border-bottom-left-radius:var(--panelRadius, 10px)
}
.nav-panel li:last-child{border:none
}
.nav-panel a{display:block;padding:0.8em 0.85em
}
.nav-panel a:hover{background-color:#151e2a;background-color:var(--lightBg, #151e2a)
}
.nav-panel a.router-link-active{font-weight:bolder;background-color:#151e2a;background-color:var(--lightBg, #151e2a)
}
.nav-panel a.router-link-active:hover{text-decoration:underline
}



