
.timeline .timeline-heading{position:relative;display:-ms-flexbox;display:flex
}
.timeline .title{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;max-width:70%
}
.timeline .loadmore-button{position:absolute;right:0.6em;font-size:14px;min-width:6em;height:1.8em;line-height:100%
}
.timeline .loadmore-text{position:absolute;right:0.6em;font-size:14px;min-width:6em;font-family:sans-serif;text-align:center;padding:0 0.5em 0 0.5em;opacity:0.8;background-color:transparent;color:rgba(185,185,186,0.5);color:var(--faint, rgba(185,185,186,0.5))
}
.timeline .loadmore-error{position:absolute;right:0.6em;font-size:14px;min-width:6em;font-family:sans-serif;text-align:center;padding:0 0.25em 0 0.25em;margin:0;color:#b9b9ba;color:var(--fg, #b9b9ba)
}
.new-status-notification{position:relative;margin-top:-1px;font-size:1.1em;border-width:1px 0 0 0;border-style:solid;border-color:var(--border, #222);padding:10px;z-index:1;background-color:#182230;background-color:var(--btn, #182230)
}



